@import '../../scss/variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 30px 0;
  box-shadow: 0px 3px 20px 0px #e7e6e6;
  position: fixed;  
  top: 0;  
  z-index: 100;  
  width: 100%;
  background-color: rgba(187, 187, 187, 0.5);
  @media (max-width: 768px) {
    padding: 20px 0;
  }

  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1400px;
    }


  &__logo {
    display: flex;
    align-items: center;



  }
  

  &__title {

    @media(max-width: 740px) {

      h1 {
        font-size: 36px;
      }
    }

    @media(max-width: 560px) {

      h1 {
        font-size: 18px;
      }

    }


  }

  &__logo-img {
    color: #353332;
    margin-right: 15px;
    font-size: 68px;

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;

    }

    p {
      color: #7b7b7b;
    }
  }
}